<template>
  <div class="loan-filter">
    <div class="loan-filter-tabs">
      <div class="loan-filter-tab"
        v-for="(filter, filterIndex) in filters"
        :class="{'active': filterIndex === tabIndex}"
        @click="onTabClick(filterIndex)">
        {{ getFilterName(filter, filterIndex) }}
        <div class="line"></div>
      </div>
    </div>

    <scalable v-if="currentFilter" height="106">
      <div class="loan-filter-content">
        <div class="option" v-for="(option, optionIndex) in currentFilter.options" :key="optionIndex"
          :class="{'checked': option.checked}"
          @click="onClick(tabIndex, optionIndex)">
          {{ option.text }}
          <img v-if="tabIndex === 2 && !option.checked" class="icon-unchecked" src="//assets.qkcdn.com/images/a91b6236a9b3d9652f4251a3f22e8051.png">
          <img v-if="tabIndex === 2 && option.checked" class="icon-checked" src="//assets.qkcdn.com/images/09a8ceebc3d6819ae3da3c297c914d2c.png">
        </div>
      </div>
    </scalable>

    <div v-if="currentFilter && tabIndex == 2" class="loan-filter-buttons">
      <div class="btn" style="margin-right: 15px;" @click="confirmSelection()">
        确认
      </div>

      <div class="btn" @click="resetSelection()">
        重置
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  @import "~scss/mixins";

  .loan-filter {
    min-height: 46px;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;

    .loan-filter-tabs {
      display: flex;
      flex: 1;

      .loan-filter-tab {
        flex: 1;
        padding: 14px 20px;
        text-align: center;
        font-size: 13px;
        line-height: 18px;
        color: #3a3a3a;
        position: relative;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;

        @include font-regular();

        &.active {
          color: #2b619b;
        }

        .line {
          position: absolute;
          top: 17px;
          right: 0;
          height: 12px;
          width: 1px;
          background: #ddd;
          transform: scale(0.5, 1);
        }

        &:last-child {
          .line {
            display: none;
          }
        }
      }
    }

    .loan-filter-content {
      padding: 20px 32px 40px 32px;

      .option {
        float: left;
        padding: 12px 19px;
        height: 56px;
        margin-right: 20px;
        margin-top: 20px;
        font-size: 22px;
        line-height: 30px;
        text-align: center;
        border: 1px solid #ddd;
        border-radius: 4px;

        @include font-regular();

        &.checked {
          background-color: #2b619b;
          color: #ffffff;
          border: none;
        }

        &:nth-child(5) {
          margin-right: 0px;
        }

        .icon-checked, .icon-unchecked {
          width: 22px;
          height: 20px;
          margin-left: 8px;
          transform: translate3d(0, 3px, 0);
        }
      }
    }

    .loan-filter-buttons {
      height: 50px;
      padding: 0 16px 16px 16px;

      .btn {
        float: left;
        width: 164px;
        height: 34px;
        border-radius: 2px;
        background-color: #ffffff;
        border: solid 1px #2b619b;
        font-size: 12px;
        line-height: 32px;
        font-weight: 600;
        text-align: center;
        color: #2b619b;
      }
    }
  }
</style>

<script>
  export default {
    props: {
      filters: Array,
      tabIndex: Number,
      onTabClick: Function,
      onSelected: Function,
      confirmSelection: Function,
      resetSelection: Function
    },

    computed: {
      currentFilter: function () {
        return this.filters[this.tabIndex]
      }
    },

    methods: {
      onClick (filterIndex, optionIndex) {
        if (this.onSelected) this.onSelected(filterIndex, optionIndex)
      },

      getFilterName (filter, filterIndex) {
        if (filterIndex === 2) {
          let t = []
          _.each(filter.options, o => {
            o.checked
              ? t.push(o.text)
              : void 666
          })

          return t.length > 0 ? t.join(' ') : filter.name
        } else {
          let o = _.find(filter.options, o => {
            return o.checked
          })

          return o && o.text !== '不限' ? o.text : filter.name
        }
      }
    }
  }
</script>



