<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <LoanListHeader
      bg-color="#1e5088"
      menu-bg-color="#1d4a7a"
      color="#ffffff"
      title="快速借钱"
      sub-title="① 注册账号 &nbsp;② 获取额度 &nbsp;③ 审批放款"
      :on-back-button-click="back"
      :expanded="true"
      :animate="true"
      :get-scroll-top="getScrollTop"
    >
      <LoanFilter slot="filter"
        :filters="filters"
        :tab-index="tabIndex"
        :on-tab-click="onFilterTabClick"
        :on-selected="onOptionSelected"
        :confirm-selection="confirmQulification"
        :reset-selection="resetQulification"
      ></LoanFilter>
    </LoanListHeader>

    <div class="page-content page-loan-list" :class="{'list-null-background': noLoans}">
      <scroller ref="scroller" style="padding-top: 156px;">
        <!-- 列表内容 -->
        <LoanCard v-for="(loan, index) in loans" :loan="loan" :key="index" @click.native="loanDetail(loan.id)"></LoanCard>
        <div style="height: 222px;"></div>
      </scroller>
      <img v-if="noLoans" src="//assets.qkcdn.com/images/8571efa39da785fc847b1710061681e5.png" class="list-null-image"/>
    </div>

    <div class="loan-filter-backdrop" :class="{'active': tabIndex > -1}" @click="hideBackdrop()" @touchstart.prevent="noop"></div>
  </div>
</template>

<style lang="scss" scoped>
  .page-loan-list {
    &.list-null-background {
      background-color: #ffffff;
    }

    .list-null-image {
      width: 375px;
      height: 280px;
      margin: 234px auto 0px;
    }
  }

  .loan-filter-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    transition: background 0.2s;

    &.active {
      background-color: rgba(0, 0, 0, 0.65);
      height: 100%;
    }
  }
</style>

<script>
  import {KEY_LOANS_FILTER, KEY_LOAN_APPLY_CLICK} from 'constants'
  import {mapState, mapActions} from 'vuex'
  import LoanListHeader from '../components/header/Index'
  import LoanCard from '../components/LoanCard'
  import LoanFilter from '../components/LoanFilter'

  export default {
    components: {
      LoanListHeader,
      LoanCard,
      LoanFilter
    },

    data () {
      return {
        filters: [
          {
            name: '金额',
            options: [
              {
                text: '不限',
                checked: false
              },
              {
                text: '1000以下',
                checked: false
              },
              {
                text: '1000~5000',
                checked: false
              },
              {
                text: '5000~10000',
                checked: false
              },
              {
                text: '10000以上',
                checked: false
              }
            ]
          },
          {
            name: '期限',
            options: [
              {
                text: '不限',
                checked: false
              },
              {
                text: '1个月以下',
                checked: false
              },
              {
                text: '1-3个月',
                checked: false
              },
              {
                text: '3-6个月',
                checked: false
              },
              {
                text: '6-12个月',
                checked: false
              },
              {
                text: '1年以上',
                checked: false
              }
            ]
          },
          {
            name: '筛选',
            options: [
              {
                text: '我没有信用卡',
                checked: false
              },
              {
                text: '我没有芝麻信用',
                checked: false
              },
              {
                text: '我没有征信报告',
                checked: false
              },
              {
                text: '我没有电商账号',
                checked: false
              }
            ]
          }
        ],

        filterResult: {
          amount: 0,
          releaseIn: 0,
          qulifications: []
        },

        tabIndex: -1,

        noLoans: false
      }
    },

    computed: {
      ...mapState('loan', [
        'loans'
      ])
    },

    created () {
      this._applyLocalFilters()
      this.fetch()
    },

    methods: {
      ...mapActions('loan', [
        'fetchLoans',
        'fetchDetail'
      ]),

      _applyLocalFilters () {
        $storage.get(KEY_LOANS_FILTER)
          ? this.filterResult = $storage.get(KEY_LOANS_FILTER)
          : void 666

        let {amount, releaseIn, qulifications} = this.filterResult

        this.filters[0].options[amount].checked = true
        this.filters[1].options[releaseIn].checked = true
        _.each(qulifications, q => {
          this.filters[2].options[q - 1].checked = true
        })
      },

      fetch () {
        let {amount, releaseIn, qulifications} = this.filterResult
        this.fetchLoans({
          amount: amount,
          releaseIn: releaseIn,
          qulifications: qulifications.join(',')
        }).then(loans => {
          this.noLoans = loans.length === 0
        })
      },

      back () {
        let fromLoanPortal = this.$route.query.from_loan_portal
        if (fromLoanPortal === 1) {
          $router.back('/tasks')
          return
        }

        $storage.remove(KEY_LOANS_FILTER)
        $router.back('/dashboard')
      },

      loanDetail (loanId) {
        if (this.tabIndex > -1) return

        $storage.set(KEY_LOANS_FILTER, this.filterResult)
        $storage.set(`${KEY_LOAN_APPLY_CLICK}_${loanId}`, false)
        this.fetchDetail(loanId).then(() => {
          $router.push(`/loan/detail/${loanId}`)
        })
      },

      onFilterTabClick (tabIndex) {
        if (this.tabIndex === tabIndex) this.tabIndex = -1
        else this.tabIndex = tabIndex
      },

      onOptionSelected (filterIndex, optionIndex) {
        if (filterIndex < 2) {
          _.each(this.filters[filterIndex].options, (o, i) => {
            o.checked = i === optionIndex
          })

          if (filterIndex === 0) { // 金额选项
            this.filterResult.amount = optionIndex
          }

          if (filterIndex === 1) { // 期限选项
            this.filterResult.releaseIn = optionIndex
          }

          this.tabIndex = -1
          this.fetch()
        }

        // 处理多选的情况
        if (filterIndex === 2) {
          this.filterResult.qulifications = []
          _.each(this.filters[filterIndex].options, (o, i) => {
            if (i === optionIndex) {
              o.checked = !o.checked
            }

            o.checked
              ? this.filterResult.qulifications.push(i + 1)
              : void 666
          })
        }
      },

      confirmQulification () { // 只是关闭选项下拉框
        this.tabIndex = -1
        this.fetch()
      },

      resetQulification () { // 清除 qulifications 中的选项
        _.each(this.filters[2].options, (o, i) => {
          o.checked = false
        })

        this.filterResult.qulifications = []
      },

      getScrollTop () {
        let {left, top} = this.$refs.scroller.getPosition() // eslint-disable-line no-unused-vars
        return top
      },

      hideBackdrop () {
        this.tabIndex = -1
      },

      noop () {}
    }
  }
</script>



