<template>
  <div class="loan-card">
    <div class="loan-card-body">
      <img class="platform-icon" :src="loan.icon">

      <div class="loan-card-title" v-text="loan.name"></div>

      <div style="height: 1px;"></div>

      <scalable width="120" height="30">
        <!-- 利率 -->
        <div class="interest-rate">
          <span class="lowlight">
            <span v-text="loan.rateType"></span>
          </span>
          <span class="highlight">
            <span v-text="loan.rate + '%'"></span>
          </span>
        </div>

        <!-- 最快放贷 -->
        <div class="release-in">
          <span class="lowlight">
            <span>最快放贷</span>
          </span>
          <span class="highlight">
            <span v-text="loan.releaseIn + loan.releaseInUnit"></span>
          </span>
        </div>
      </scalable>

      <div class="loan-amount">
        <scalable>
          <!-- 金额 -->
          <div class="amount">
            <span v-text="loan.amount"></span>
          </div>

          <!-- 单位 -->
          <div class="unit">
            额度范围(元)
          </div>
        </scalable>
      </div>
    </div>

    <div class="thin-border-bottom"></div>

    <div class="loan-card-footer">
      <scalable class="tags">
        <div style="height: 24px;"></div>
        <span class="tag" v-for="tag in loan.tags">{{ tag }}</span>
      </scalable>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  @import "~scss/mixins";

  $orange: #fc7421;
  $grey: #b5b5b5;

  .thin-border-bottom {
    @include thin-border-bottom(#eee, 0);
  }

  .loan-card {
    height: 120px;
    padding: 0 0 0 20px;
    margin: 0 0 10px 0;
    background-color: #FFF;

    .loan-card-body {
      padding: 13px 20px 11px 58px;
      height: 75px;

      .loan-card-title {
        @include font-regular();

        font-size: 14px;
        line-height: 20px;
        color: #3a3a3a;
      }

      .platform-icon {
        float: left;
        margin-top: 2px;
        margin-left: -58px;
        width: 48px;
        height: 48px;
        border-radius: 4px;
      }

      .interest-rate, .release-in {
        height: 28px;

        .highlight, .lowlight {
          font-size: 22px;
          line-height: 28px;
          height: 28px;
          float: left;

          @include font-regular();
        }

        .lowlight {
          color: $grey;
          margin-right: 16px;
        }

        .highlight {
          color: $orange;
        }
      }

      .interest-rate {
        margin-bottom: 3px;
      }

      .loan-amount {
        width: 180px;
        height: 42px;
        float: right;
        text-align: right;
        margin-top: -42px;

        .amount {
          color: $orange;
          font-size: 40px;
          line-height: 56px;

          @include font-semibold();
        }

        .unit {
          color: $grey;
          font-size: 22px;
          line-height: 28px;

          @include font-regular();
        }
      }
    }

    .loan-card-footer {
      height: 45px;

      .tags {
        .tag {
          float: left;
          border: 1px solid #ddd;
          border-radius: 4px;
          text-align: center;
          font-size: 22px;
          line-height: 30px;
          padding: 4px 12px;
          margin-right: 10px;
          color: $grey;

          @include font-regular();
        }
      }
    }
  }
</style>

<script>
  export default {
    props: {
      loan: Object
    }
  }
</script>



